import {createSlice } from '@reduxjs/toolkit';

const initialState = {
  path: '/'
}

export const menuSlice = createSlice({
  name: 'menu',
  initialState,
  reducers: {
    goto: (state, action) => {
      state.path = action.payload
    }
  }
})

export const { goto } = menuSlice.actions

export const currentMenu = (state) => state.menu.path

export default menuSlice.reducer