import { useDispatch, useSelector } from "react-redux";
import { currentMenu, goto } from "./menuSlice";
import {Menu as AntdMenu} from "antd";
import routes from "../../app/router";
import { Link } from "react-router-dom";


export function Menu() {
  const current = useSelector(currentMenu);
  const dispatch = useDispatch()

  const handleClick = (e) => {
    console.log(e)
    dispatch(goto(e.key))
  }

  return (
    <AntdMenu 
      style={{
        overflow: 'hidden',
        height: '100vh',
        position: 'fixed',
        width: '201px',
        left: 0,
      }}
      onClick={handleClick}
      defaultSelectedKeys={[current]}
      mode="inline">
        {routes.map(v => <AntdMenu.Item key={'mitm'+v.path}>
          <Link to={v.path} key={'link'+v.path}>
            {v.label}
          </Link>
        </AntdMenu.Item>)}
    </AntdMenu>
  )
}