import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { getQuestsFromDB, getQuestLvFromDB } from '../../app/db';

const initialState = {
  quests: [],
  levels: [],
  loading: false
}

export const getQuestAsync = createAsyncThunk(
  'quests/getQuest',
  async () => {
    const quests = await getQuestsFromDB()

    return quests
  }
)

export const getQuestLvAsync = createAsyncThunk(
  'quests/getQuestLv',
  async () => {
    const lvs = await getQuestLvFromDB()

    return lvs
  }
)

export const questSlice = createSlice({
  name: 'quest',
  initialState,
  reducers: {
    getQuests: (state) => {
      state.quests = []
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(getQuestAsync.pending, (state) => {
        state.loading = true;
      })
      .addCase(getQuestLvAsync.pending, (state) => {
        state.loading = true;
      })
      .addCase(getQuestAsync.fulfilled, (state, action) => {
        state.quests = action.payload
        state.loading = false
      })
      .addCase(getQuestLvAsync.fulfilled, (state, action) => {
        state.levels = action.payload
        state.loading = false
      })
  },
})

export const {getQuests}  = questSlice.actions

export const questList = (state) => state.quest.quests
export const questLv = (state) => state.quest.levels
export const isLoading = (state) => state.quest.loading

export default questSlice.reducer