import { Button, List, Popover, Space, Table, Tag } from "antd"
import Text from "antd/lib/typography/Text"
import { useEffect, useRef, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { getQuestAsync, getQuestLvAsync, isLoading, questList, questLv } from "./questSlice"
import { VList } from 'virtual-table-ant-design'
import Search from "antd/lib/input/Search"
import { useHotkeys } from 'react-hotkeys-hook'
import { InfoCircleOutlined } from '@ant-design/icons';

let lastIndex = 0
const updateIndex = () => {
  lastIndex++
  return lastIndex
}

export default function Quest() {
  const o_quests = useSelector(questList)
  const [quests, setQuest] = useState(o_quests)
  const loading = useSelector(isLoading)
  const levels = useSelector(questLv)
  const dispatch = useDispatch()
  const searchInput = useRef()



  const columns = [
    {
      title: '',
      key: `detail${updateIndex()}`,
      render: (_, r) => {
        let rewardColumns = [
          {
            title: '메인퀘스트 보수 첫째 줄',
            key: `detail_A${updateIndex()}`,
            children: [
              {
                title: '아이템', 
                key: `item_A${updateIndex()}`,
                render: (_, r) => 
                  <Space direction="vertical">
                    <Text>{r.ko_A}</Text>
                    <Text type="secondary">{r.jp_A}</Text>
                  </Space>
              },
              {
                title: '수량',
                key: `qty_A${updateIndex()}`,
                dataIndex: 'qty_A'
              },
              {
                title: '확률',
                key: `percent_A${updateIndex()}`,
                dataIndex: 'percent_A'
              }
            ]
          },
          {
            title: '메인퀘스트 보수 둘째 줄',
            key: `detail_B${updateIndex()}`,
            children: [
              {
                title: '아이템', 
                key: `item_B${updateIndex()}`,
                render: (_, r) => 
                  <Space direction="vertical">
                    <Text>{r.ko_B}</Text>
                    <Text type="secondary">{r.jp_B}</Text>
                  </Space>
              },
              {
                title: '수량',
                key: `qty_B${updateIndex()}`,
                dataIndex: 'qty_B'
              },
              {
                title: '확률',
                key: `percent_B${updateIndex()}`,
                dataIndex: 'percent_B'
              }
            ]
          },
          {
            title: '서브퀘스트 보수',
            key: `detail_C${updateIndex()}`,
            children: [
              {
                title: '아이템', 
                key: `item_C${updateIndex()}`,
                render: (_, r) => 
                  <Space direction="vertical">
                    <Text>{r.ko_C}</Text>
                    <Text type="secondary">{r.jp_C}</Text>
                  </Space>
              },
              {
                title: '수량',
                key: `qty_C${updateIndex()}`,
                dataIndex: 'qty_C'
              },
              {
                title: '확률',
                key: `percent_C${updateIndex()}`,
                dataIndex: 'percent_C'
              }
            ]
          },
          {
            title: '메인퀘스트 보수 셋째 줄',
            key: `detail_D${updateIndex()}`,
            children: [
              {
                title: '아이템', 
                key: `item_D${updateIndex()}`,
                render: (_, r) => 
                  <Space direction="vertical">
                    <Text>{r.ko_D}</Text>
                    <Text type="secondary">{r.jp_D}</Text>
                  </Space>
              },
              {
                title: '수량',
                key: `qty_D${updateIndex()}`,
                dataIndex: 'qty_D'
              },
              {
                title: '확률',
                key: `percent_D${updateIndex()}`,
                dataIndex: 'percent_D'
              }
            ]
          }
        ]
        
      
        return (<Popover title="퀘스트 보수" content={
            <Table
              dataSource={r.rewards} 
              columns={rewardColumns} 
              rowKey={() => `prow${updateIndex()}`} 
              size="small" 
              pagination={false}/>} 
            placement="right"
            trigger="hover" >
          <InfoCircleOutlined />
        </Popover>)
      },
      width: 30
    },
    {
      title: '레벨',
      key: `lv${updateIndex()}`,
      dataIndex: 'lv',
      filters: levels.map(v => {
        return {
          text: v,
          value: v
        }
      }),
      width: 120,
      onFilter: (value, record) => record.lv.indexOf(value) === 0,
    },
    {
      title: '퀘스트',
      key: `name${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">
        <div>
          <Tag color="green">{r.type}</Tag>
          { r.key && <Tag color="red">{r.key}</Tag> }
          { r.talk !== 0 && <Tag color="gold">대화</Tag> }
        </div>
        <Text>{r.ko}</Text>
        <Text type="secondary">{r.jp}</Text>
      </Space>,
      filters: [
        {text: '긴급', value: '긴급' },
        {text: '키', value: '키' }
      ],
      onFilter: (value, record) => record.key === value
    },
    {
      title: '맵',
      key: `map${updateIndex()}`,
      dataIndex: 'map',
      width: 150
    },
    {
      title: '메인 타겟',
      key: `goal${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">
        <Text>{r.goal}</Text>
        <Text type="secondary">보수: {r.reward}z / {r.reward / 10}pt / {r.hrp}HRP</Text>
      </Space>,
      width: 500,
      responsive: ['xxl']
    },
    {
      title: '서브 타겟',
      key: `goal2${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">
        <Text>{r.goal2}</Text>
        { r.goal2 !== '없음' && <Text type="secondary">보수: {r.reward2}z / {r.reward2 / 10}pt / {r.hrp2}HRP</Text> }
        
      </Space>,
      width: 500,
      responsive: ['xxl']
    }
  ]

  useEffect(() => {
    dispatch(getQuestLvAsync())
    dispatch(getQuestAsync())
  }, [dispatch])

  useEffect(() => {
    setQuest(o_quests)
  }, [o_quests])

  const onSearch = (value) => {
    setQuest(o_quests.filter(v => v.ko.includes(value)))
  }
  useHotkeys('ctrl+f', (e) => {
    searchInput.current.focus({
      cursor: 'all',
    })
    e.preventDefault();  
  });
  const onKeyDownSearch = (e) => {
    if (e.keyCode === 114 || (e.ctrlKey && e.keyCode === 70)) {        
        e.preventDefault();         
        searchInput.current.focus({
          cursor: 'all',
        })   
    }    
  }

  return (
    <>
      <Space style={{ marginBottom: 16 }}>
        <Search placeholder="퀘스트 명 검색" 
          allowClear onSearch={onSearch} style={{ width: 400 }} 
          onKeyDown={onKeyDownSearch}
          ref={searchInput}/>
      </Space>
      <Table columns={columns} dataSource={quests} pagination={false} 
      loading={loading}
      rowKey='id' 
      scrollToFirstRowOnChange={true}
      scroll={{ y: '80vh' }}
      components={VList({
        height: 1000,
      })}/>
    </>
  )
}